.home {
  height: 100vh;
  text-align: center;
  background-color: var(--site-main-bg-color);
}

.home-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}

.home-button {
  display: inline-block;
}

.home-button button {
  width: 150px;
  height: 50px;
  font-size: 28px;
  line-height: 32px;
  color: var(--site-main-font-color);
  background-color: var(--site-main-bg-color);
  border: 1px solid var(--site-main-color);
  cursor: pointer;
}

.home-buttons li + li {
  margin-top: 20px;
}



