.synthesize {
  position: relative;
}

.synthesize-header {
  position: absolute;
  top: 20px;
  left: 20px;
  color: var(--site-main-font-color);
  background-color: var(--site-main-bg-color);
  border: 1px solid var(--site-main-color);
  cursor: pointer;
}

.synthesize-container {
  display: -ms-flexbox;
  display: flex;
  font-family: 'sans-serif';
  text-align: center;
  min-height: 100vh;
}

.synthesize-echarts {
  -ms-flex: 0 0 50%;
      flex: 0 0 50%;
  background-color: var(--site-main-bg-color);
  color: var(--site-main-font-color);
  border-right: 1px solid var(--site-second-color);
}

.synthesize-d3 {
  -ms-flex: 0 0 50%;
      flex: 0 0 50%;
  background-color: var(--site-main-bg-color);
  color: var(--site-main-font-color);
}



